
$HeroBg : url('../../assets/images/hero-bg.jpg');

.hero-bg{
    background: $HeroBg;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    height: 75vh;
    position: relative;
    > *{
        z-index: 1;
        position: relative;
    }
    .hero-body{
        height: 100%;
        align-items: center;
        h1{
            font-family: $font-top-modern;
            font-size: 92px;
            color: $olivine;
            font-weight: bold;
            .duality{
                font-family: $font-duality;
                color: $white;
            }
            .havelock-blue{
                color:$havelock-blue
            }
            
        }
    }
    &:after{
        content: '';
        position: absolute;
        background: linear-gradient(45deg, $olivine, $havelock-blue, $olivine, $havelock-blue);
        width: 100%;
        height: 100%;
        opacity: 0.5;
        top:0;
        left: 0;
    };
}